@import url("https://fonts.googleapis.com/css2?family=Lora:wght@500&display=swap");

.aboutItems {
  display: flex;
  height: 100vh;
  align-items: center;
  align-items: center;
  justify-content: center;
}

.aboutItem {
  width: 50%;
  text-align: center;
}

.aboutPara {
  font-size: 1.7em;
  font-family: "Lora", serif;
  text-align: center;
}

.about {
  position: relative;
  height: 100vh;
  width: 100vw;
  color: white;
}

.skillsTable {
  margin-right: auto;
  margin-left: auto;
  border: 1px solid white;
}

.skillsTable th {
  font-size: 1.7em;
}

.skillsTable td {
  font-size: 1em;
}

@media screen and (max-width: 700px) {
  .aboutItems {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    bottom: 10%;
  }
  .aboutItem {
    width: 100%;
    border: 1px solid green;
    text-align: center;
  }

  .aboutPara {
    font-size: 1em;
    font-family: "Lora", serif;
  }
}
