@import url(https://fonts.googleapis.com/css2?family=Lora:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Serif&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Shippori+Mincho+B1&display=swap);
#pageProfile {
  color: white;
  position: relative;
  width: 100vw;
}

header {
  position: relative;
  width: 100%;
  float: right;
}

.navItem:visited {
  color: white;
}

#title {
  height: 93vh;
  display: flex;
  align-items: center;
  font-family: "Lora", serif;
  width: 100%;
}

.titleItems {
  height: 150px;
  font-weight: bolder;

  /* box-shadow: 2px 3px 5px; */
  margin: 30px; /* animation: borderEffect 2s linear infinite; */
}
.titleItems p {
  font-size: 4em;
  color: #222f6e;
}

.titleItems span {
  font-size: 100px;
}

img {
  width: 30px;
  height: 30px;
}

a:visited {
  color: white;
}

.navBar {
  display: flex;
  position: fixed;
  width: 100%;
  z-index: 2;
}
.navBar:link {
  text-decoration: none;
}
.navItem {
  font-size: 2em;
  padding: 5px;
  text-decoration: none;
}

.scrollLinks {
  width: 60%;
  background-color: red;
  color: #15212b;
}

.externalSiteLinks {
  text-align: right;
  margin-right: 1%;
  width: 40%;
}

@media screen and (min-width: 320px) {
  .scrollLinks {
    width: 100%;
    background-color: red;
    color: #15212b;
  }
}

.aboutItems {
  display: flex;
  height: 100vh;
  align-items: center;
  align-items: center;
  justify-content: center;
}

.aboutItem {
  width: 50%;
  text-align: center;
}

.aboutPara {
  font-size: 1.7em;
  font-family: "Lora", serif;
  text-align: center;
}

.about {
  position: relative;
  height: 100vh;
  width: 100vw;
  color: white;
}

.skillsTable {
  margin-right: auto;
  margin-left: auto;
  border: 1px solid white;
}

.skillsTable th {
  font-size: 1.7em;
}

.skillsTable td {
  font-size: 1em;
}

@media screen and (max-width: 700px) {
  .aboutItems {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    bottom: 10%;
  }
  .aboutItem {
    width: 100%;
    border: 1px solid green;
    text-align: center;
  }

  .aboutPara {
    font-size: 1em;
    font-family: "Lora", serif;
  }
}

.cardHome {
  position: relative;
  width: 100%;
  border: 1px solid red;
  text-align: center;
  display: flex;
  height: 33vh;
  color: white;
}
.cardName {
  font-size: 2em;
  text-align: center;
  color: white;
}
.cardHome > div {
  width: 100%;
}

.details {
  position: relative;
  text-align: center;
}
.projectImage {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
}
.cardImg {
  padding: 0;
  height: 80%;
  width: 50%;
  padding-right: 10px;
}

.viewProjectBtn {
  background-color: green;
  color: white;
  border-radius: 5px;
}

.viewCodeBtn {
  background-color: gray;
  color: white;
  border-radius: 5px;
}

.viewProjectBtn:hover {
  background-color: white;
  color: green;
}

.viewCodeBtn:hover {
  color: grey;
  background-color: white;
}

@media screen and (max-width: 700px) {
  .cardHome {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
  }

  .details {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .cardName {
    color: white;
    position: relative;
    margin-bottom: 0%;
    font-size: 1em;
  }

  .cardImg {
    width: 200px;
  }

  .details > div {
    height: 50%;
    margin-bottom: 0%;
  }
}

/*card component page*/
.projects {
  position: relative;
  width: 100vw;
}

.cardSection {
  width: 90%;
}

.projectDiv {
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (min-width: 320px) {
  #projectSubtitle {
    top: 41px;
  }
  .projectDiv {
    position: relative;
    top: 30px;
  }
}

* {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}
.pages {
  list-style-type: none;
  position: relative;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}
.pageProfile {
  position: relative;
  width: 100vw;
  height: 100vh;
  background-color: #15212b;
}
.subTitle {
  z-index: 1;
  position: relative;
  font-size: 2em;
  font-family: monospace;
  text-align: center;
  color: white;
}
#about {
  height: 80vh;
}
#projects {
  height: 100%;
}

a:visited {
  color: white;
}

a:link {
  color: white;
}

@media screen and (max-width: 700px) {
  .subTitle {
    top: 8%;
    z-index: 1;
    position: relative;
    font-size: 2em;
    font-family: monospace;
    text-align: center;
    color: white;
  }
}

.contactSection {
  background-color: #15212b;
}
.contactInfo {
  display: flex;
  align-items: center;
  justify-content: center;
}

* {
  padding: 0;
  margin: 0;
}

#body {
  width: 100%;
  height: 100vh;
}

#mainNav {
  width: 100%;
  height: 15%;
}
#navTitle,
#navTitle:visited {
  color: green;
  font-size: 25px;
}

#titleLink:hover {
  text-decoration: none;
}

#recipes {
  height: 100%;
  width: 100%;
}

#select {
  width: 100%;
  height: 100%;
}

#choices {
  width: 100%;
}

#meatSelectTitle {
  color: #ff4a3b;
}

#recipeComp {
  width: 100%;
}
/* RecipeCards css  */
.recipeGrid {
  width: 100%;
  margin: 5rem auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
}

.btnPic,
.meatChoiceBtn {
  height: 100px;
  width: 100px;
  border-radius: 25px;
}

.boxOuter {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}
.boxLabel {
  font-family: "PT Serif", serif;
}
.boxIngredients {
  font-family: "Quicksand", sans-serif;
}
.ingredients h3 {
  font-family: "Quicksand", sans-serif;
  font: bold;
}

.close {
  position: fixed;
  z-index: 1;
  font-size: 28px;
  color: red;
}

.boxHead {
  border: 2px solid green;
  align-items: center;
  z-index: 1;
}

.box {
  padding: 10px;
  height: 600px;
  width: 900px;
  background-color: white;
  border: 1px solid red;
  overflow: scroll;
  position: relative;
}
.cardRecipe:hover,
.cardRecipe:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.ingredients {
  overflow: auto;
  position: relative;
}

.boxImg {
  height: 500px;
  width: 320px;
  float: right;
  border-radius: 5px;
}

.cardRecipe {
  align-items: center;
  justify-items: center;
  height: 100%;
  width: 100%;
}
.cardRecipe:hover {
  box-shadow: 5px 10px 8px 10px #888888;
}

#home {
  position: relative;
  height: 100vh;
  width: 100%;
  background-color: white;
}
#homePageTitle {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Noto Sans", sans-serif;
  font-size: 2em;
  font: bolder;
  color: green;
}
.titles {
  font-size: 2.5em;
  font-family: "Shippori Mincho B1", serif;
}

#featuredRecipes {
  position: relative;
  width: 100%;
  height: 100%;
}

#featuredRecipeCards {
  position: relative;
  width: 100%;
}

.gridItem {
  background-color: white;
  border: 1px solid #ffc171;
}

.homeRecipeGrid {
  width: 100%;
  border: 2px solid green;
  margin: 2rem auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
}

