.cardHome {
  position: relative;
  width: 100%;
  border: 1px solid red;
  text-align: center;
  display: flex;
  height: 33vh;
  color: white;
}
.cardName {
  font-size: 2em;
  text-align: center;
  color: white;
}
.cardHome > div {
  width: 100%;
}

.details {
  position: relative;
  text-align: center;
}
.projectImage {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
}
.cardImg {
  padding: 0;
  height: 80%;
  width: 50%;
  padding-right: 10px;
}

.viewProjectBtn {
  background-color: green;
  color: white;
  border-radius: 5px;
}

.viewCodeBtn {
  background-color: gray;
  color: white;
  border-radius: 5px;
}

.viewProjectBtn:hover {
  background-color: white;
  color: green;
}

.viewCodeBtn:hover {
  color: grey;
  background-color: white;
}

@media screen and (max-width: 700px) {
  .cardHome {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
  }

  .details {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .cardName {
    color: white;
    position: relative;
    margin-bottom: 0%;
    font-size: 1em;
  }

  .cardImg {
    width: 200px;
  }

  .details > div {
    height: 50%;
    margin-bottom: 0%;
  }
}
