* {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}
.pages {
  list-style-type: none;
  position: relative;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}
.pageProfile {
  position: relative;
  width: 100vw;
  height: 100vh;
  background-color: #15212b;
}
.subTitle {
  z-index: 1;
  position: relative;
  font-size: 2em;
  font-family: monospace;
  text-align: center;
  color: white;
}
#about {
  height: 80vh;
}
#projects {
  height: 100%;
}

a:visited {
  color: white;
}

a:link {
  color: white;
}

@media screen and (max-width: 700px) {
  .subTitle {
    top: 8%;
    z-index: 1;
    position: relative;
    font-size: 2em;
    font-family: monospace;
    text-align: center;
    color: white;
  }
}
