.navBar {
  display: flex;
  position: fixed;
  width: 100%;
  z-index: 2;
}
.navBar:link {
  text-decoration: none;
}
.navItem {
  font-size: 2em;
  padding: 5px;
  text-decoration: none;
}

.scrollLinks {
  width: 60%;
  background-color: red;
  color: #15212b;
}

.externalSiteLinks {
  text-align: right;
  margin-right: 1%;
  width: 40%;
}

@media screen and (min-width: 320px) {
  .scrollLinks {
    width: 100%;
    background-color: red;
    color: #15212b;
  }
}
